
import axios from '../../axios'
const accessToken = {
  headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
}


export default {
  addApplication ({ commit }, item) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        url: 'application/insert',
        data: item,
        headers: {
          'Content-Type': 'multipart/form-data',
          'Authorization': `Bearer ${localStorage.getItem('accessToken')}`
        }
    
      }).then((response) => {
        commit('ADD_ITEM_APPLICATION', response.data.data)
        resolve(response)
      })
      .catch((error) => { reject(error) })
    })
  },
  showApplication ({ commit }, item) {
    return new Promise((resolve, reject) => {
      axios.get(`application/${item.id}/show`, accessToken)
        .then((response) => {
          commit('SET_APPLICATION', response.data.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  deleteApplication ({commit}, itemId){
    return new Promise((resolve, reject) => {
      axios.delete(`application/${itemId}/delete`, accessToken)
        .then((response) => {
          commit('REMOVE_ITEM', itemId)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  }
}