<template>
    <div class="content-table">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{ headerCard }}</span>
            </div>
            <el-row>
                <el-col v-loading="loading">
                    <el-form class="form-application-add" label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px">
                        <el-form-item label="Application" prop="application">
                            <el-input v-model="ruleForm.application" autocomplete="off" placeholder="please insert name Application"></el-input>
                        </el-form-item>
                        <el-form-item label="Package Name" prop="package">
                            <el-input v-model="ruleForm.package" autocomplete="off" placeholder="please insert Package"></el-input>
                        </el-form-item>
                        <el-form-item label="Icon">
                            <el-upload
                              class="upload-demo"
                              action=""
                              ref="upload"
                              list-type="picture"
                              :multiple="false"
                              :limit="1"
                              :file-list="fileList"
                              name="file"
                              :auto-upload="false">
                            <el-button size="small" type="primary">Click to upload</el-button>
                            <div slot="tip" class="el-upload__tip">jpg/png files with a size less than 1024kb</div>
                            </el-upload>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row> 
            <el-row>
                <el-col>
                    <div class="btn-input-add">
                    <el-tooltip class="item" effect="dark" content="Please Check You'r input Data, and Submit" placement="top">                     
                      <el-button size="small" @click="submitForm('ruleForm')">Submit</el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="If You'r Cancel, and redirect main application!" placement="top">
                      <el-button size="small" type="danger" @click="resetForm()">Cancel</el-button>
                    </el-tooltip>
                    </div>
                </el-col>
                <el-col>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>

import moduleApplication from '../../../store/applications/moduleApplication.js'

import router from '@/router'

export default {
    data () {
        var validateApplication = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('Please input Name Application'));
          } else {
            callback();
          }
        };
        var validatePackage = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('Please input Package Application'));
          } else {
            callback();
          }
        };
        return {
            loading : false,
            ruleForm : {
                application : '',
                package : ''
            },
            rules: {
            application: [
                { validator: validateApplication, trigger: 'blur' }
            ],
            package : [
                { validator: validatePackage, trigger: 'blur' }
            ],
            },
            fileList : [],
            formData : new FormData(),
        }
    },
    computed : {
        baseUrl () {
            const path = this.$route.path
            return path[0]
        },
        headerCard () {
            return this.$route.name
        }
        
    },

    methods : {
      submitForm(formName) {
        this.loading = true
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const obj = JSON.stringify({
              id_user : this.$store.state.userDataActive.id,
              application : this.ruleForm.application,
              package : this.ruleForm.package
            });
            this.formData.append('file', this.$refs.upload.uploadFiles[0].raw)
            this.formData.append('item', obj)
            if (!moduleApplication.isRegistered) {
              this.$store.registerModule('dataApplications', moduleApplication)
              moduleApplication.isRegistered = true
            }
            this.$store.dispatch('dataApplications/addApplication', this.formData)
              .then((Response) => {
                if (Response.data.success == true ){
                  this.$notify({
                    title: 'Success',
                    message: Response.data.message,
                    type: 'success',
                    offset: 100
                  });
                  router.push('/application')
                  this.loading = false
                } else {
                  this.$notify.error({
                    title: 'Error',
                    message: Response.data.message,
                    offset: 100
                  });
                  this.$refs.upload.uploadFiles = []
                  this.loading = false
                }
              })
          } else {
            this.loading = false
            this.$notify.error({
              title: 'Error',
              message: 'Invalid Data Input',
              offset: 100
            });
          }
        });
      },
      resetForm() {
        this.ruleForm.application = null,
        this.ruleForm.package = null,
        this.$refs.upload.uploadFiles = null
        if (!this.$refs.upload.uploadFiles) {
          router.push('/application')
        }
      }
    },
}
</script>

<style>
.form-application-add {
    margin-right: 25px;
    margin: 0 auto;
    width: 600px;
}
.content-table {
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
}
.btn-input-add {
    margin: 0 auto;
    width: 250px;
}
</style>
