
export default {

    ADD_ITEM_APPLICATION (state, item) {
        state.applications.unshift(item)
    },
    SET_APPLICATION (state, items) {
        state.applications = items
    },  
    REMOVE_ITEM (state, itemId) {
        const ItemIndex = state.applications.findIndex((p) => p.id_applications === itemId)
        state.applications.splice(ItemIndex, 1)
    }

}
  